import React, { Component } from 'react';
import RaisedButton from 'material-ui/RaisedButton';
import Checkbox from 'material-ui/Checkbox';
import Slider from 'material-ui/Slider';
import Subheader from 'material-ui/Subheader';
import ActionSettings from 'material-ui/svg-icons/action/settings'
import ImageTexture from 'material-ui/svg-icons/image/texture'

const styles = {
    openButton: {
        position: 'absolute',
        bottom: '1rem',
        right: '1rem',
        border:'2px solid white',
    },
    modal: {
        position: 'absolute',
        bottom: 0,
        left: 0,
        right: 0,
        backgroundColor: 'white',
        padding: '0.5rem',
    },
    modalItem: {
        padding: '0.5rem',
    },
    slider: {
        marginTop: 0,
        marginBottom: '0.5rem',
    },
    detectOptions: {
        display: 'flex',
        flexFlow: 'row',
        flexWrap: 'wrap',
    },
    detectOptionItem: {
        boxSizing: 'border-box',
        width: '50%',
        padding: '0 0.5rem',
    },
    detectOptionItemFull: {
        boxSizing: 'border-box',
        width: '100%',
        padding: '0 0.5rem',
    },
    detectEdges: {
        marginBottom: '1rem',
    },
    iconstyle:{
    		marginRight:0,
    },
};

class ProperySettings extends Component {
    state = {
        open: false,
    };

    handleOpen = () => {
        setTimeout(() => {
            this.setState({ isOpen: true });
        }, 500);
    }

    handleClose = () => {
        setTimeout(() => {        
            this.setState({ isOpen: false });
        }, 500);
    }

    render() {
        const { isOpen } = this.state;
        if (!isOpen) {
            return <RaisedButton 
            				style={styles.openButton}
            				primary 
            				onClick={this.handleOpen} 
            				label="属性设置"
            				labelPosition="after"
            				icon={<ActionSettings />}
            			/>
        }

        const {
            blur,
            lowTreshold,
            highTreshold,
            opacity,
            isDetectingEdge,
            onBlurChange,
            onLowTresholdChange,
            onHighTresholdChange,
            onOpacityChange,
            onDetectEdgeChange
        } = this.props;

        return (
            <div style={styles.modal}>
                { !isDetectingEdge &&
                    <div style={styles.modalItem}>
                    		<ImageTexture style={styles.iconstyle}/>
                        <Subheader>Opacity: {opacity}</Subheader>
                        <Slider
                            name="透明度"
                            value={opacity}
                            onChange={onOpacityChange}
                            sliderStyle={styles.slider}
                        />
                    </div>
                }
                {
                    isDetectingEdge && (
                        <div style={styles.detectOptions}>
                            <div style={styles.detectOptionItemFull}>
                                <Subheader>模糊度: {blur}</Subheader>
                                <Slider
                                    name="blur"
                                    value={blur}
                                    max={4}
                                    onChange={onBlurChange}
                                    sliderStyle={styles.slider}
                                />
                            </div>
                            <div style={styles.detectOptionItem}>
                                <Subheader>low treshold: {lowTreshold}</Subheader>
                                <Slider
                                    name="lowTreshold"
                                    value={lowTreshold}
                                    max={127}
                                    onChange={onLowTresholdChange}
                                    sliderStyle={styles.slider}
                                />
                            </div>
                            <div style={styles.detectOptionItem}>
                                <Subheader>high treshold: {highTreshold}</Subheader>
                                <Slider
                                    name="highTreshold"
                                    value={highTreshold}
                                    max={127}
                                    onChange={onHighTresholdChange}
                                    sliderStyle={styles.slider}
                                />
                            </div>
                        </div>
                    )
                }
                <Checkbox
                    name="isDetectingEdge"
                    type="checkbox"
                    checked={isDetectingEdge}
                    onCheck={onDetectEdgeChange}
                    label="检测边缘"
                    style={styles.detectEdges}
                />

                <RaisedButton
                    primary
                    fullWidth
                    onClick={this.handleClose}
                    label="关闭"
               
                />
            </div>
        );
    }
}


export default ProperySettings;



